import React from 'react';
import { Illustration } from 'react-zdog';
import { RotatingAnchor, ShapeExtrusion } from '../src';

const Index = () => (
  <div style={{width: '800px', height: '800px'}}>
    <Illustration>
        <RotatingAnchor>
            <ShapeExtrusion spacing={{x: 1, y: 1, z: 1}}/>
        </RotatingAnchor>
    </Illustration>
  </div>
);

export default Index;